import React, { Component } from 'react'
import CommandLogger from './Components/CommandLogger'
import { omit } from 'lodash'

export class App extends Component {
  constructor(props = {}, context = {}) {
    let screen = props.screen

    super(omit(props, 'screen'), context)

  }

  componentDidMount () {
  }

  render () {

    return (
      <box label="skypager" {...(borderStyles('magenta'))}>
        <CommandLogger  ref="server"
                        top="1%"
                        height="95%"
                        label="deepstream server"
                        left="2%"
                        width="40%"
                        cmd='skypager serve'
                        {...(borderStyles('green'))}
                        />

        <CommandLogger  ref="webpack"
                        top="1%"
                        label="webpack dev server"
                        height="80%"
                        left="42%"
                        width="57%"
                        cmd='skypager dev --theme dashboard-dark --proxy-target="localhost:6020" --proxy-path="/engine.io"'
                        options={{cwd:'/Users/jonathan/Skypager'}}
                        {...(borderStyles('cyan'))}
                        />

        <box label='console' top="82%" height="16%" left="42%" width="57%" {...(borderStyles('yellow'))}>
          repl
        </box>
      </box>
    )
  }
}

export default App


function borderStyles(color = 'white', style = 'line') {
  return {
    border : {
      type: style
    },
    style : {
      border: {
        fg: color
      }
    }
  }
}



